@font-face {
  font-family: "Balsamiq";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/BalsamiqSans-Regular.woff2") format("woff2"), url("../fonts/BalsamiqSans-Regular.woff") format("woff");
}
@font-face {
  font-family: "Balsamiq";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/BalsamiqSans-Bold.woff2") format("woff2"), url("../fonts/BalsamiqSans-Bold.woff") format("woff");
}
@font-face {
  font-family: "Raleway";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Raleway-Regular.woff2") format("woff2"), url("../fonts/Raleway-Regular.woff") format("woff");
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 15px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 35px;
  }
}
@media (min-width: 1024px) {
  .container {
    padding: 0 135px;
  }
}

.link-animation {
  color: #ffffff;
  text-decoration: none;
  transition-duration: 300ms;
  transition-property: opacity;
}
.link-animation:hover {
  cursor: pointer;
  opacity: 0.7;
}
.link-animation:active {
  cursor: pointer;
  opacity: 0.3;
}

.page {
  width: 100%;
  height: 100%;
  margin: 0 auto;
  overflow-x: hidden;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  font-family: "Raleway", sans-serif;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-width: 375px;
  min-height: 100%;
  margin: 0;
  color: #000000;
  background-color: #ffffff;
}

.header {
  background-color: #14855F;
  color: #ffffff;
}

.header__container {
  position: relative;
  padding-bottom: 200px;
}
.header__container::before {
  position: absolute;
  content: "";
  width: 274px;
  height: 180px;
  background-image: url("../img/background/main-img-left.svg");
  left: 0;
  bottom: 0;
  background-repeat: no-repeat;
}
.header__container::after {
  position: absolute;
  content: "";
  width: 357px;
  height: 301px;
  background-image: url("../img/background/main-img-right.svg");
  right: 0;
  top: 156px;
  background-repeat: no-repeat;
}

.header__intro {
  padding-top: 100px;
  display: flex;
}

.header__description-title {
  padding: 0;
  margin: 0 0 9px;
  font-family: "Balsamiq", sans-serif;
  font-size: 20px;
  line-height: 24px;
}

.header__description-text {
  padding: 0;
  margin: 0;
  max-width: 290px;
}

.header__title {
  padding: 0;
  margin: 28px auto 0 0;
  font-family: "Balsamiq", sans-serif;
  font-weight: 700;
  font-size: 100px;
  line-height: 94px;
  max-width: 50%;
  text-align: center;
  position: relative;
}
.header__title::before {
  position: absolute;
  content: "";
  width: 295px;
  height: 111px;
  background-image: url("../img/background/main-img-top.svg");
  left: -270px;
  top: 100px;
  background-repeat: no-repeat;
}
.header__title::after {
  position: absolute;
  content: "";
  width: 669px;
  height: 260px;
  background-image: url("../img/background/main-img-bottom.svg");
  left: -35px;
  top: 225px;
  background-repeat: no-repeat;
}

.header__title-special {
  color: #FFB8B8;
}

.header__button {
  font-weight: 500;
  background-color: transparent;
  border: none;
  padding: 38px;
  margin: 0 0 -130px;
  color: #ffffff;
  font-size: 14px;
  line-height: 24px;
  align-self: flex-end;
  border: 1px solid #ffffff;
  border-radius: 50%;
}

.navigation {
  padding: 30px 0;
}

.navigation__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navigation__list-menu {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
}

.navigation__list-contacts {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: flex;
}

.navigation__item-menu {
  font-weight: 500;
  text-transform: uppercase;
}

.navigation__link {
  font-weight: 600;
  color: #ffffff;
  text-decoration: none;
  padding: 10px;
}

.navigation__menu-link {
  color: #ffffff;
  text-decoration: none;
  padding: 14px;
}

.navigation__item-contacts {
  border: 1px solid #ffffff;
  border-radius: 50%;
  padding: 8px 0 5px;
  margin-left: 10px;
}

.navigation__contacts-link {
  padding: 10px;
  margin: 0;
}